main {
  background-image: url("https://images.unsplash.com/photo-1493976040374-85c8e12f0c0e?w=1920&q=80");
  background-repeat: repeat;
  background-size: cover;
  background-attachment: fixed;
}

.questions-hero {
  background: linear-gradient(135deg, rgba(220, 20, 60, 0.9) 0%, rgba(26, 26, 26, 0.95) 100%), url("https://images.unsplash.com/photo-1492571350019-22de08371fd3?w=1920&q=80");
  background-size: cover;
  background-position: center;
  padding: 60px 0;
  margin-bottom: 40px;
}

.questions-hero-content {
  text-align: center;
  color: white;
}

.questions-title {
  font-size: 3rem;
  font-weight: 700;
  margin-bottom: 15px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}
.questions-title i {
  color: #ffd700;
}

.questions-subtitle {
  font-size: 1.2rem;
  opacity: 0.95;
}

.q-box-modern {
  background: rgba(255, 255, 255, 0.98);
  backdrop-filter: blur(10px);
  border-radius: 30px;
  padding: 50px 40px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.15);
  margin-bottom: 40px;
}

.question-header {
  margin-bottom: 40px;
}

.question-number {
  display: inline-flex;
  align-items: center;
  padding: 12px 30px;
  background: linear-gradient(135deg, rgba(220, 20, 60, 0.1) 0%, rgba(220, 20, 60, 0.05) 100%);
  border: 2px solid rgba(220, 20, 60, 0.3);
  border-radius: 25px;
  font-size: 1.1rem;
  font-weight: 700;
  color: #dc143c;
  margin-bottom: 20px;
}
.question-number i {
  font-size: 1.2rem;
}

.progress-bar-container {
  width: 100%;
  height: 8px;
  background: rgba(220, 20, 60, 0.1);
  border-radius: 10px;
  overflow: hidden;
  margin-top: 15px;
}

.progress-bar-fill {
  height: 100%;
  background: linear-gradient(90deg, #dc143c 0%, #b71c1c 100%);
  border-radius: 10px;
  transition: width 0.5s ease;
  box-shadow: 0 2px 10px rgba(220, 20, 60, 0.3);
}

.question-text {
  text-align: center;
  margin-bottom: 40px;
}
.question-text h2 {
  font-size: 2rem;
  font-weight: 700;
  color: #1a1a1a;
  line-height: 1.4;
}

.answers-container {
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-bottom: 40px;
}

.answer-option {
  position: relative;
}

.answer-checkbox {
  position: absolute;
  opacity: 0;
  width: 0;
  height: 0;
}
.answer-checkbox:checked + .answer-label {
  background: linear-gradient(135deg, rgba(220, 20, 60, 0.1) 0%, rgba(220, 20, 60, 0.05) 100%);
  border-color: #dc143c;
  color: #dc143c;
}
.answer-checkbox:checked + .answer-label .answer-check-icon {
  opacity: 1;
  transform: scale(1);
}

.answer-label {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 25px;
  background: white;
  border: 2px solid #e0e0e0;
  border-radius: 15px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 1.1rem;
  font-weight: 600;
  color: #1a1a1a;
}
.answer-label:hover {
  border-color: rgba(220, 20, 60, 0.5);
  background: rgba(220, 20, 60, 0.02);
  transform: translateX(5px);
  box-shadow: 0 4px 15px rgba(220, 20, 60, 0.1);
}

.answer-text {
  flex: 1;
}

.answer-check-icon {
  opacity: 0;
  transform: scale(0.5);
  transition: all 0.3s ease;
  color: #dc143c;
  font-size: 1.2rem;
}

.form-actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  margin-top: 40px;
  padding-top: 30px;
  border-top: 2px solid rgba(220, 20, 60, 0.1);
  flex-wrap: wrap;
}

.btn-search-now {
  display: inline-flex;
  align-items: center;
  padding: 12px 25px;
  background: #6c757d;
  color: white;
  border: none;
  border-radius: 25px;
  font-weight: 600;
  font-size: 0.95rem;
  transition: all 0.3s ease;
  cursor: pointer;
  box-shadow: 0 4px 15px rgba(108, 117, 125, 0.3);
}
.btn-search-now:hover {
  background: #5a6268;
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(108, 117, 125, 0.4);
  color: white;
}

.btn-next,
.btn-search-final {
  display: inline-flex;
  align-items: center;
  padding: 14px 35px;
  background: linear-gradient(135deg, #dc143c 0%, #b71c1c 100%);
  color: white;
  border: none;
  border-radius: 25px;
  font-weight: 700;
  font-size: 1.1rem;
  transition: all 0.3s ease;
  cursor: pointer;
  box-shadow: 0 4px 15px rgba(220, 20, 60, 0.4);
}
.btn-next:hover,
.btn-search-final:hover {
  background: linear-gradient(135deg, #b71c1c 0%, #8b0000 100%);
  transform: translateY(-2px);
  box-shadow: 0 6px 25px rgba(220, 20, 60, 0.5);
  color: white;
}

.btn-search-final {
  background: linear-gradient(135deg, #ffd700 0%, #ffb300 100%);
  color: #1a1a1a;
  box-shadow: 0 4px 15px rgba(255, 215, 0, 0.4);
}
.btn-search-final:hover {
  background: linear-gradient(135deg, #ffb300 0%, #ff9800 100%);
  box-shadow: 0 6px 25px rgba(255, 215, 0, 0.5);
}

.next-button-wrapper {
  margin-left: auto;
}

@media (max-width: 768px) {
  .questions-title {
    font-size: 2rem;
  }
  .q-box-modern {
    padding: 30px 20px;
  }
  .question-text h2 {
    font-size: 1.5rem;
  }
  .form-actions {
    flex-direction: column;
  }
  .form-actions .btn-search-now,
  .form-actions .btn-next,
  .form-actions .btn-search-final {
    width: 100%;
    justify-content: center;
  }
  .form-actions .next-button-wrapper {
    margin-left: 0;
    width: 100%;
  }
}